import {
  	loginByPhone,
  	logout,
  	getInfo,
} from 'api/login';
import {
  	getToken,
  	setToken,
  	removeToken,
} from 'utils/auth';

import dateFormat from 'utils/dateFormat'

const user = {
  	state: {
	    user: '',
	    token: '',
	    roles: '',
	    menus: undefined, // 菜单元素
	    elements: undefined, // 可操作元素
	    permissionMenus: undefined,  // 权限菜单
	    setting: {},
  	},
	
  	mutations: {
	    // 更新全局--token
	    SET_TOKEN: (state, token) => {
	      	state.token = token;
	    },
	    // 更新全局--设置
	    SET_SETTING: (state, setting) => {
	      state.setting = setting;
	    },
			// 更新全局--角色
	    SET_ROLES: (state, roles) => {
	      state.roles = roles;
	    },
	    // 更新全局--菜单
	    SET_MENUS: (state, menus) => {
	      state.menus = menus;
	    },
	    // 更新全局--元素标签
	    SET_ELEMENTS: (state, elements) => {
	      state.elements = elements;
	    },
	    // 更新全局--登录菜单权限
	    SET_PERMISSION_MENUS: (state, permissionMenus) => {
	      state.permissionMenus = permissionMenus;
	    },

  	},
	
  	actions: {
	    // 手机登录
	    LoginByPhone({
	      	commit
	    }, userInfo) {
	    	
	      	const username = userInfo.name.trim();
	      	commit('SET_TOKEN', '');
	      	commit('SET_ROLES', '');
	      	commit('SET_MENUS', undefined);
      		commit('SET_ELEMENTS', undefined);
	      	removeToken();
	      	return new Promise((resolve, reject) => {
		        loginByPhone({name:username,pass:userInfo.pass}).then(response => {
		          	const data = response;
//		          	console.log('data.result.loginName::'+JSON.stringify(data))
		          	setToken(data.result);
		          	commit('SET_TOKEN', data.result.operator.id);
		          	
		          	resolve();
		        }).catch(error => {
		          	reject(error);
		        });
	      	});
	    },
	
	
	
	    // 登出
	    LogOut({
	      	commit
	    }) {
	      	return new Promise((resolve, reject) => {
		        logout().then(() => {
		          	commit('SET_TOKEN', '');
		          	commit('SET_ROLES', []);
		          	commit('SET_MENUS', undefined);
		          	commit('SET_ELEMENTS', undefined);
		          	commit('SET_PERMISSION_MENUS', undefined);
		          	removeToken();
		          	removeShopowner();
		          	resolve();
		        }).catch(error => {
		          	reject(error);
		        });
	      	});
	    },
	
	    // 前端 登出
	    FedLogOut({
	      	commit
	    }) {
	      	return new Promise(resolve => {
		        commit('SET_TOKEN', '');
		        commit('SET_MENUS', undefined);
		        commit('SET_ELEMENTS', undefined);
		        commit('SET_PERMISSION_MENUS', undefined);
		        removeToken();
		        resolve();
	      	});
	    },
	    
  	}
};

export default user;



