<template>
	
  	<div class="create_wrapper">
  		<el-tabs type="border-card">
			<el-tab-pane>
			    <span slot="label"><i class="el-icon-date"></i>权限分配</span>
			      <!--table-->
			    <el-row v-if="tableFlag"> 
			    	<el-col :span="24">
			    		<el-table   :data="tableData" style="width: 100%">
			    			<el-table-column  prop="name" label="姓名">
					        </el-table-column>
					        <el-table-column prop="department" label="功能权限" >    
					        </el-table-column>
					        <el-table-column  prop="post" label="岗位">
					        </el-table-column>
					    </el-table>
			    	</el-col>
			    </el-row>
			</el-tab-pane>
			  	<div class="add_created">
			    	<i class="el-icon-plus" @click="createDialogVisible = true"></i>
			    	<p>创建角色</p>
			    </div>
			    
			  
			
		</el-tabs>
	    <!--创建角色弹出框-->
	    <el-dialog
			    title="请创建角色"
			    :visible.sync="createDialogVisible"
			    width="40%"
			    :before-close="handleClose">
			 	<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
			 		<el-form-item label="创建者姓名" prop="name">
				    	<el-input v-model="ruleForm.name" style="width:90%;"></el-input>
				    </el-form-item>
				    <el-form-item label="筛选条件" prop="query">
				    	<el-cascader
						    :options="cascaderData"
						    v-model="ruleForm.query"
						    style="width:90%;"
						   >
						</el-cascader>
				    </el-form-item>
			 	</el-form>
		    <span slot="footer" class="dialog-footer">
			    <el-button @click="createDialogVisible = false">取 消</el-button>
			    <el-button type="primary" @click="addTreeFun('ruleForm')">确 定</el-button>
		  	</span>
		</el-dialog>
  	</div>
</template>

<script>
//	import cascader from './cascader'
//	import tableData from './table'
  	export default {
	    name: 'creatRole',
	    data() {
	      	return {
		        createDialogVisible: false,
				cascaderData:cascader,
				tableData:tableData,
				tableFlag:false,
				//创建者弹出框
				ruleForm:{
					name:'',
					query: [],   //联动框model值
				},
				rules: {
				 	name: [
			            { required: true, message: '请输入创建者姓名', trigger: 'blur' },
			            { min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur' }
			        ],
			        query: [
			            { required: true, message: '请选择你的筛选条件', trigger: 'blur' },
			        ],
				}
	      	}
	    },
	    computed: {
	    	
	    },
	    methods:{
	    	//添加角色确定
	    	addTreeFun(formName){

	    		this.$refs[formName].validate((valid) => {
					if (valid) {
	    				this.createDialogVisible = false
	    				this.tableFlag= true
	    				this.tableData.push({
	    					department:this.ruleForm.query.map(item => item).join('/'), name: this.ruleForm.name, post: '前端'})
	    				
					} else {
						console.log('error submit!!');
						return false;
					}
				});
	    	
	    	},
	    	handleClose(done) {
		        this.$confirm('确认关闭？')
		          .then(() => {
		            done();
		          })
		          .catch(() => {});
		      	}
			}
  	}
</script>

<style rel="stylesheet/scss" lang="scss" >
	.create_wrapper{
		height: 100%;
		.add_created{
			background: #eee;
			width: 150px;
			padding: 25px 0;
			border-radius: 6px;
			
			text-align: center;
			/*font-size:60px ;*/
			margin-top: 15px;
			i{
				cursor:pointer ;
				font-size:60px ;
			}
		}
	}
</style>



