// dashboard
export default [
	{
        path: '/',
        redirect: {
            name: 'login'
        }
    },
    {
        path: '/dashboard',
        name: 'dashboard',
        meta: {
            title: 'Dashboard'
        },
        component: require('../views/dashboard/index.vue')
    }
]



